<template>
    <!--注册内容-->
    <div id="_register">
        <div class="_register-box d-inline-block px-3 mb-3">
            <!--注册标题栏-->
            <div class="txt-subtitle py-3" style="font-weight:bold">新用户注册</div>
            <!--注册提示栏-->
            <div class="d-flex justify-content-center py-1">
                <el-steps :active="stepActive" finish-status="success">
                    <el-step title="验证手机号"></el-step>
                    <el-step title="填写帐号信息"></el-step>
                    <el-step title="注册成功"></el-step>
                </el-steps>
            </div>
            <!--注册表单栏-->
            <div id="_form-box">
                <div class="_form-box1 active  d-flex flex-column justify-content-between align-items-center py-4 px-2">
                    <input type="text" class=" py-1 my-1" placeholder="手机号码" title="手机号码不应该小于11位" id="userPhone"
                           name="userPhone" maxlength="11" minlength="11"
                           style="text-indent:8px;width:82%;" required/>
                    <input type="submit" value="点击按钮进行验证" class="txt-subbody py-2 my-3"
                           style="width:82%;" id="_clickVerify" @click="onVerifyMyselfCode()">

                    <div class="_verify-code d-flex flex-row justify-content-between align-items-center"
                         style="width:82%;">
                        <div class="d-inline">
                            <span>验证码:</span>
                            <input type="text" id="userVerifyCode" name="userVerifyCode"
                                   style="height:30px; width:100px;text-indent:5px;" maxlength="4" minlength="4"
                                   class="mx-1" required/>
                            <div class="d-none" id="_hiddenTxt"></div>
                        </div>
                        <canvas id="myCanvas" height="50" width="120">当前浏览器不支持canvas，暂无法验证</canvas>
                    </div>
                    <div class="_goto-login txt-mini  my-1" @click="$router.push({path:'/login'})" style="width:82%;">
                        <span style="float:left">已经有账号，立即登录</span>
                    </div>
                    <input type="submit" value="下一步" @click="next(0)" class="text-white py-2 my-2 "
                           style="background:red;width:82%;">
                </div>
                <div class="_form-box2 d-flex flex-column justify-content-between align-items-center py-4 px-2">
                    <input type="text" class=" py-1 my-1" placeholder="用户名" id="userAccount" name="userAccount"
                           title="用户名不能为空"
                           style="text-indent:8px;width:82%;" required/>
                    <input type="password" class=" py-1 my-1" placeholder="密码" id="userPwd" name="userPwd" title="密码不应该小于6位"
                           style="text-indent:8px;width:82%;" required/>
                    <input type="password" class=" py-1 my-1" placeholder="确认密码" id="userPwdConcern" name="userPwdConcern"
                           title="两次密码必需相同"
                           style="text-indent:8px;width:82%;" required/>
                    <input type="text" class=" py-1 my-1" placeholder="电子邮箱" id="userEmail" name="userEmail"
                           title="请输入真实有效的邮箱"
                           style="text-indent:8px;width:82%;" required/>

                    <div class="_goto-login txt-mini my-1" @click="$router.push({path:'/login'})" style="width:82%;">
                        <span style="float:left" class="_goto-login-text">已经有账号，立即登录</span>
                    </div>
                    <input type="submit" value="下一步" @click="next(1)" class="text-white py-1 my-2 "
                           style="background:red;width:82%;">
                </div>
                <div class="_form-box3 d-flex flex-column justify-content-between align-items-center py-4 px-2">
                    <div class="d-flex flex-column justify-content-center align-items-center"
                         style="width:300px;height:300px">
                        <i class="iconfont icon-chenggong" style="font-size:60px;color:red; "></i>
                        <span class="text-danger text-subbody mt-3"
                              style="letter-spacing:1px;font-weight:bold;">注册成功!</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery';
    import {ElMessage} from 'element-plus';

    import {getRegister} from 'network/user'
    import {ref, reactive, toRefs, onMounted} from 'vue'
    import {useRouter} from 'vue-router'

    export default {
        name: "Register",
        setup() {
            const router = useRouter();
            // 用户的输入信息
            const userinfo = reactive({
                name: "",
                password: '',
                password_confirmation: '',
                email: ''
            });

            let stepActive = ref(0);          // 步聚条

            onMounted(()=>{
                initVerifyCode();
            });


             // 创建自定义的验证码
            const initVerifyCode = ()=>{
                var canvas = document.getElementById("myCanvas");   // 拿到画布
                var _hiddentNode = document.getElementById("_hiddenTxt");      // 隐藏的验证码         //其实这种方法很容易受到攻击

                if (canvas.getContext) {                  //检查支持性
                    var ctx = canvas.getContext("2d");   // 拿到画笔，获得绘制上下文的绘画功能

                    drawCodeValid();
                    canvas.onclick = drawCodeValid;

                    //获取随机颜色
                    function randomColor(min, max) {
                        return "rgba(" + randomRange(min, max) + ',' + randomRange(min, max) + ',' + randomRange(min, max) + ",1)";
                    }

                    // 获取随机数
                    function randomRange(min, max) {     //   5 - 8
                        return Math.floor(Math.random() * (max - min + 1) + min);   // 0- 3 + 5  ===> 5-8
                    }                                 //   0 - 10     ===> 0 - 11
                                                      // -45 - 45     ===> 0 - 91 -45

                    function drawCodeValid() {

                        var number = 4;
                        var txt = "";
                        var cHeight = canvas.height;
                        var cWidth = canvas.width;

                        // 画布绘制
                        ctx.save();
                        ctx.fillStyle = randomColor(180, 220);
                        ctx.beginPath();
                        ctx.fillRect(0, 0, cWidth, cHeight);
                        ctx.restore();


                        // 数字绘制
                        ctx.Baseline = "bottom";                              // 文字底部对齐
                        for (var i = 0; i < number; i++) {
                            var deg = randomRange(-45, 45);
                            var x = (cWidth - 10) / number * i + 10;
                            var y = randomRange(cHeight / 2, cHeight - 10);
                            var color = randomColor(40, 120);
                            var currentNum = randomRange(0, 10).toString()[0];
                            txt += currentNum;
                            ctx.save();
                            ctx.fillStyle = color;                             //颜色必需比背景颜色更深
                            ctx.font = randomRange(25, 40) + "px SimHei";
                            ctx.translate(x, y);
                            ctx.rotate(deg * Math.PI / 180);                        // 移动,旋转画布
                            ctx.beginPath();
                            ctx.fillText(currentNum, 0, 0);
                            ctx.restore();
                        }

                        // 小黑点绘制
                        for (var j = 0; j < number * 30; j++) {
                            var xCircle = randomRange(0, cWidth);
                            var yCircle = randomRange(0, cHeight);
                            ctx.save();
                            ctx.fillStyle = randomColor(40, 120);     // 颜色偏深
                            ctx.beginPath();
                            ctx.arc(xCircle, yCircle, 1, 0, 360 * Math.PI / 180);        // 绘制小圆
                            ctx.closePath();
                            ctx.fill();                              // 自动闭合路径
                            ctx.restore();
                        }

                        // 划线绘制
                        for (var k = 0; k < number * 2; k++) {
                            var xLineStart = randomRange(0, cWidth);
                            var yLineStart = randomRange(0, cHeight);
                            var xLineEnd = randomRange(0, cWidth);
                            var yLineEnd = randomRange(0, cHeight);
                            ctx.save();
                            ctx.strokeStyle = randomColor(80, 140);

                            ctx.beginPath();
                            ctx.moveTo(xLineStart, yLineStart);           // 抬起画笔
                            ctx.lineTo(xLineEnd, yLineEnd);               // 绘制划线
                            ctx.stroke();                                // 绘制路径
                            ctx.restore();
                        }
                        _hiddentNode.innerHTML = txt;            // 隐藏的验证码
                    }

                } else {
                    alert('当前Javascript不支持画布元素');
                }
            };

             // 验证自定义的验证码
            const onVerifyMyselfCode = ()=>{
                $('._form-box1>div:eq(0)').removeClass('active').addClass('active');
            };


            //  eduwork         123456   12345600@qq.com
            //  smallcat        123456   778899@qq.com



            // 简单验证邮箱是否正确
            const onVerifySimplyEmail = (newString) => {
                if(newString.indexOf('@')!==-1 && newString.endsWith('.com')!==false  && newString.lastIndexOf('@')<newString.lastIndexOf('.com')-2){
                    return true;
                }else{
                    return '请输入正确的邮箱';
                }
            };


            // 下一步操作
            const next = (num) => {
                var isOk = false;
                if (num === 0) {
                    var inputPhone = $('#userPhone').val().replace(' ', '');
                    var inputPhoneInt = parseInt(inputPhone,10);
                    var inputVerifyCode = $('#userVerifyCode').val().replace(' ', '');
                    var standardVerifyCode = $('#_hiddenTxt').html().replace(' ', '');
                    let feedBackMessage = '';
                    if (inputPhone.length !== 11) {
                        feedBackMessage = '电话号码必需等于11位';
                    } else {

                        if (inputPhoneInt.toString().length !== 11) {
                            feedBackMessage = '电话号码含有非数字';
                        } else {
                            if (inputVerifyCode !== standardVerifyCode) {
                                feedBackMessage = '验证码不正确';
                                $('#myCanvas').click();
                            }
                        }
                    }
                    if (!feedBackMessage.length) {
                        $("#_form-box>div:eq(1)").addClass('active').siblings('div').removeClass('active');
                        isOk = true;
                        ElMessage.success('手机号码验证成功！');
                    } else {
                        ElMessage.error(feedBackMessage);
                    }

                } else if (num === 1) {
                    var userAccount = $('#userAccount').val().replace(' ', '');
                    var userPwd = $('#userPwd').val().replace(' ', '');
                    var userPwdConcern = $('#userPwdConcern').val().replace(' ', '');
                    var userEmail = $('#userEmail').val().replace(' ', '');
                    let feedBackMessage = '';
                    if (userAccount.length === 0) {
                        feedBackMessage = '用户名不能为空';
                    } else  if (userPwd.length === 0) {
                        feedBackMessage = '密码不能为空';
                    } else if (userPwdConcern.length === 0) {
                        feedBackMessage = '确认密码不能为空';
                    }else if (userEmail.length === 0) {
                        feedBackMessage = '邮箱不能为空';
                    }else if (userPwd.length < 6) {
                        feedBackMessage = '密码长度不能为小于6';
                    }else if(userPwd !== userPwdConcern){
                        feedBackMessage = '两次输入的密码不一致';
                    }else{
                        var res_email = onVerifySimplyEmail(userEmail);
                        if(res_email!==true){
                            feedBackMessage = res_email;
                        }
                    }

                    if (!feedBackMessage.length) {
                        userinfo.name = userAccount;
                        userinfo.password = userPwd;
                        userinfo.password_confirmation = userPwdConcern;
                        userinfo.email = userEmail;
                        $("#_form-box>div:eq(2)").addClass('active').siblings('div').removeClass('active');
                        isOk = true;
                        ElMessage.success('注册的帐号信息填写成功！');
                    } else {
                        ElMessage.error(feedBackMessage);
                    }

                }
                if (stepActive.value < 3 && isOk)
                    stepActive.value++;
                if(stepActive.value ===2)
                    onSubmit();
            };

            // 提交表单数据
            const onSubmit = () => {
                    getRegister(userinfo).then(res => {
                        if (res.status === 201) {
                            ElMessage.success('注册成功！即将跳转登录页面！');
                            setTimeout(() => {
                                router.push({path: '/login'});
                            })
                        }
                        userinfo.password = '';            // 清空数据
                        userinfo.password_confirmation = '';
                        userinfo.email = '';
                    }).catch(err => {
                        console.log('register-err',err);
                        ElMessage.error('注册失败!请重试');
                    });
            };

            return {...toRefs(userinfo), onSubmit, next, stepActive, onVerifyMyselfCode};
        },
        components: {}
    }
</script>

<style lang="scss">
    #_register {
        min-height: 540px;
        display: flex;
        justify-content: center;
        align-items: center;

        ._register-box {
            width: 400px;
            border: 1px solid pink;
            box-shadow:0 0 2px red;

            #_form-box {
                min-height: 300px;

                input {
                    border: 1px solid #EDB1B1;
                    border-radius: 5px;
                    outline: none;
                }

                div._goto-login > span:hover {
                    color: red;
                }
            }

            #_form-box > div {
                display: none !important;
            }

            #_form-box > div.active {
                display: flex !important;
            }
            ._form-box1 div._verify-code{
                display:none!important;
            }
            ._form-box1 div._verify-code.active{
                display:flex!important;
            }
        }

        canvas {
            border: 1px solid black;
            border-radius: 5px;
        }
    }

    .el-steps--horizontal {
        white-space: nowrap;
        width: 300px;
    }

    .el-step.is-horizontal .el-step__line {
        width: 80px;
        height: 2px;
        top: 11px;
        left: 40px;
        right: 0;
    }

    .el-step__head {
        display: flex;
        justify-content: start;
        align-items: center;
    }

    .el-step__title {
        color: #6F5757 !important;
        font-size: 12px;
        margin-left: -16px;
    }

    /*.el-step__main {
        white-space: normal;
        text-align: center;
    }*/
</style>